import {
  MOVIE_LIST_REQUEST,
  MOVIE_LIST_SUCCESS,
  MOVIE_LIST_FAIL,
  MOVIE_FEATURED_FAIL,
  MOVIE_FEATURED_REQUEST,
  MOVIE_FEATURED_SUCCESS,
} from "./../constants/movieConstants";
export const movieListReducer = (state = { movies: [] }, action) => {
  switch (action.type) {
    case MOVIE_LIST_REQUEST:
      return {
        loading: true,
        movies: [],
      };
    case MOVIE_LIST_SUCCESS:
      return {
        loading: false,
        movies: action.payload,
      };
    case MOVIE_LIST_FAIL:
      return {
        loading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};

export const movieFeaturedReducer = (
  state = {
    loading: false,
    featured: {},
    // lastVideoId: null,
  },
  action
) => {
  switch (action.type) {
    case MOVIE_FEATURED_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case MOVIE_FEATURED_SUCCESS:
      return {
        ...state,
        loading: false,
        featured: action.payload,
        // lastVideoId: action.lastVideoId,
      };
    case MOVIE_FEATURED_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};
